@media screen and (max-width: 1200px) {
  .logo {
    flex-direction: row;
  }
}
@media screen and (max-width: 1025px) {
  .main__layout__wrapper-content {
    flex-direction: column;
  }

  .main__layout__wrapper-content__left,
  .main__layout__wrapper-content__main {
    width: 100%;
    min-width: 100%;
  }

  .link__wrapper.is_settings {
    flex-direction: column;
  }

  .link__delete {
    position: absolute;
    right: -15px;
    top: -65px;
    cursor: pointer;
  }

  .link__up, .link__down {
    right: -15px;
  }

  form label {
    align-self: center;
  }
}
@media screen and (max-width: 691px) {
  .profile__link__wrapper {
    max-width: 100%;
  }
}
@media screen and (max-width: 321px) {
  .link__texts {
    overflow: hidden;
  }

  .link__delete {
    position: absolute;
    right: -1px;
    top: 100px;
    cursor: pointer;
  }
}

@keyframes shake {
  10%,
  90% {
    transform: translate3d(0, -1px, 0);
  }

  20%,
  80% {
    transform: translate3d(0, 1px, 0);
  }

  30%,
  50%,
  70% {
    transform: translate3d(0, -3px, 0);
  }

  40%,
  60% {
    transform: translate3d(0, 3px, 0);
  }
}
